.App {
  min-width: 100vw;
  min-height: 100vh;
  width: 100vw;
  height:  100vh;
  background-color: #feffff;
  display: flex;
  overflow-x: hidden;
  padding: 0px 40px;
}

.menuContainer{
  width: 100px;
  height: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  position: fixed;
  top: 0;
  padding: 30px 15px;
}

.subMenu {
  height: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-evenly;
}

.logo {
  width: 70px;
  min-width: 40px;
  height: 70px;
  min-height: 40px;
  border-radius: 10px;
}

.iconContainer {
  width: 50px;
  height: 50px;
  min-width: 40px;
  min-height: 40px;
  border-radius: 40%;
  border: 2px solid #717273;
  display: flex;
  justify-content: center;
  align-items: center;
  cursor: pointer;
  box-shadow: 0px 5px 20px rgba(0,0,0,0.15);
  margin: 15px 0;
  transition: 0.5s;
}

/* .MuiSvgIcon-root {
  color: #313233;
  font-size: 18px;
} */

.iconContainer:hover , .iconContainer.active{
  background-color: #2e2f30;
  color: #feffff;
}

main{
  flex: 1;
  height: 100%;
  padding-top: 35px;
  margin-left: 130px;
}

.searchBox{
  display: flex;
  align-items: center;
  justify-content: center;
  width: 100%;
  background: #feffff;
  border: 2px solid #757677;
  padding: 8px 15px;
  border-radius: 15px;
  box-shadow: 0px 0px 20px rgba(0,0,0,0.2);
}

.searchBox input{
  height: 100%;
  width: 100%;
  outline: none;
  border: none;
  color: #313233;
  font-size: 18px;
}

.search{
  height: 25px;
  cursor: pointer;
  border-radius: 50%;
}

.mainContainer{
  flex: 1;
  height: auto;
  margin-top: 15px;
  display: grid;
  grid-template-columns: repeat(auto-fill, 300px);
  grid-auto-rows: 10px;
  justify-content: center;
}

.pin {
  border-radius: 16px;
  background: #444;
  margin: 5px;
  overflow: hidden;
  cursor: pointer;
}

.pin .mainPic {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.pin .content{
  width: 100%;
  height: 50px;
  background: rgba(255,255,255,0.2);
  bottom: -55px;
  backdrop-filter: blur(5px);
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0px 15px;
}

.pin .content h3 {
  color: #feffff;
  font-size: 18px;
  font-weight: 500;
}

.pin:hover .content {
  margin-top: -55px;
  bottom: 0;
  transition: 0.3s;
}

.pin.small{
  grid-row-end: span 26;
}

.pin.medium{
  grid-row-end: span 33;
}

.pin.large{
  grid-row-end: span 45;
}

@media screen and (max-width: 480px) {
  .App{
    padding: 0px 20px;
  }

  .mainContainer {
    padding: 30px 30px;
    width: unset;
    left: 15px;
  }

  main {
    margin-left: 75px;
  }
  
}